<template lang="html">
  <div class="card daycard today" :class="{outside: month != refMonth}">
    <div class="cardimg daytop">
      <div class="daytext"><strong> {{ day }}</strong><div class="weekday">{{ weekDay }}</div></div>
      <div class="addentry">
        <p><strong> +</strong></p>
      </div>
    </div>
    <div class="container user">
      <ul class="userlist">
        <li>
          <div class="round">
            <p>SU</p>
          </div>
          <div class="longuser" v-if="windowWidth > 1200">
            some user
          </div>
        </li>
        <li>
          <div class="round">
            <p>SO</p>
          </div>
          <div class="longuser" v-if="windowWidth > 1200">
            some other user
          </div>
        </li>
      </ul>
    </div>
  </div>

</template>

<script>
export default {
  props: {
    date: Date,
    refMonth: Number
  },
  data: function() {
    return {
      daysOfWeek: ['Sun','Mon','Tue','Wed','Thu','Fri','Sat'],
      weekDay: '',
      day: 0,
      month: 0
    }
  },
  created() {
    this.weekDay = this.daysOfWeek[this.date.getDay()];
    this.day = this.date.getDate();
    this.month = this.date.getMonth();
  }
}
</script>

<style lang="css">
.round {
  position: relative;
  left: -10px;
  height: 25px;
  width: 25px;
  background-color: #dd0404;
  border-radius: 50%;
  display: inline-block;
  float: left;
  border: 3px solid #222;
}
.longuser {
  display: inline-block;
  text-align: left;
}
.round p {
  position: relative;
  text-align: center;
  color: #eee;
}
.outside {
  color: #555;
  background-color: #999;
}
.outside .daytext {
  text-shadow:
    -1px -1px 0 #222,
    1px -1px 0 #222,
    -1px 1px 0 #222,
    1px 1px 0 #222;
  z-index: 1;
}
.outside li div {
  background-color: #999;
}
.daycard {
  height: auto;
  display: inline-block;
  color: #dd0404;

}
.daytop {
  height: 70px;
  border-bottom: 1px dotted;
}
.daytext {
  font-size: 25px;
  position: relative;
  display: inline-block;
  float: right;
  right: 3px;
  text-shadow:
    -1px -1px 0 #fff,
    1px -1px 0 #fff,
    -1px 1px 0 #fff,
    1px 1px 0 #fff;
  z-index: 1;
}
.weekday {
  display: block;
  bottom: 0px;
  font-size: 15px;
  text-decoration: overline;
}
.addentry{
  display: inline-block;
  position: absolute;
  background-color: #dd0404;
  color: #eee;
  font-size: 40px;
  border-radius: 5px;
  margin-bottom: 0px;
  text-align: right;
  top: 15px;
  left: 5px;
  width: 45px;
  height: 30px;
}
.addentry p {
  top: -15px;
  text-align: center;
  position: relative;
}
.addentry:hover {
  color: #aaffaa;
  box-shadow: inset -7px -7px 15px -10px #aaffaa;
  cursor: pointer;
}
.user {
  font-size: 12px;
}
.today {
  border-radius: 5px;
  border-color: #dd0404;
}
.userlist {
  list-style-type: none;
  padding: 0;
  overflow: none;
}

.userlist li {
  position: relative;
  display: block;
  float: left;
  color: #444;
}
@media screen and (max-width: 400px) {
  .daycard {
    width: 40px;
    font-size: 10px;
    color: #000;
  }
  .weekday {
    display: block;
    bottom: 0px;
  }
  .addentry {
    top: 15px;
    left: 5px;
    width: 30px;
    height: 30px;
  }
  .daytext {
    text-shadow: 0 0 0;
  }
  .outside .daytext {
    text-shadow: 0 0 0;
  }
}
@media screen and (min-width: 1200px) {
  .daycard {
    width: 150px;
    font-size: 15px;
    left: 0px;
  }
  .weekday {
    display: inline-block;
  }
  .addentry {
    left: -2px;
    top: -3px;
    width: 30px;
    height: 30px;
    box-shadow: inset -11px -11px 2px -10px #222;
  }
}
</style>
